import useSWR from "swr";
import axios from "axios";
import { useAuth } from "@/ui/auth/useAuth";

/**
 * API Response Âûã
 */
export interface ItemDetailResponse {
  item: {
    id: number;
    name: string;
    price: number;
    explain: string | null;
    remain: number;
    user_id: number;
    shop_id: number;

    brands: string[];
    brand_primary: string | null;
    condition: string | null;
    color: string | null;

    tags: any[];
    item_image: string | null;
  };
  comments: any[];
  is_favorited: boolean;
  favorites_count: number;
}

/**
 * Item Detail SWR
 * - key „ÅØ itemId „ÅÆ„ÅøÔºàauth / guest „ÇíÂàÜ„Åë„Å™„ÅÑÔºâ
 * - Ê•ΩË¶≥Êõ¥Êñ∞„ÉªÂÜçÊ§úË®º„Å®ÂÆåÂÖ®‰∏ÄËá¥
 */
export const useItemDetailSWR = (itemId: number | null) => {
  const { apiClient, isAuthenticated, isReady } = useAuth();

  /**
   * fetch Êù°‰ª∂
   */
  const shouldFetch =
    typeof itemId === "number" && Number.isFinite(itemId) && isReady;

  /**
   * ‚úÖ SWR KeyÔºà„Åì„Çå„ÅåÊúÄÈáçË¶ÅÔºâ
   * auth / guest „ÇíÂàÜ„Åë„Å™„ÅÑ
   */
  const swrKey = shouldFetch ? ["item-detail", itemId] : null;

  /**
   * fetcher
   */
  const fetcher = async (): Promise<ItemDetailResponse> => {
    if (!itemId) {
      throw new Error("itemId is not available");
    }

    // Ë™çË®ºÊ∏à„ÅøÔºàJWT / CookieÔºâ
    if (apiClient) {
      const res = await apiClient.get<ItemDetailResponse>(`/items/${itemId}`);
      return res.data;
    }

    // Êú™„É≠„Ç∞„Ç§„É≥ÔºàguestÔºâ
    const res = await axios.get<ItemDetailResponse>(`/api/items/${itemId}`);
    return res.data;
  };

  /**
   * SWR Êú¨‰Ωì
   */
  const { data, error, isLoading, mutate } = useSWR<ItemDetailResponse>(
    swrKey,
    fetcher,
    {
      // ‚ùå ÂãùÊâã„Å´Êàª„ÇãÂéüÂõ†„Å´„Å™„ÇãÊåôÂãï„ÅØÂÖ®„Å¶ OFF
      revalidateOnFocus: false,
      revalidateOnReconnect: false,
      revalidateIfStale: false,
      shouldRetryOnError: false,
    },
  );

  /**
   * View Áî®„Å´Ê≠£Ë¶èÂåñ„Åó„Å¶Ëøî„Åô
   */
  return {
    // core
    item: data?.item ?? null,
    comments: data?.comments ?? [],

    // ‚ù§Ô∏è reaction
    isFavorited: data?.is_favorited ?? false,
    favoritesCount: data?.favorites_count ?? 0,

    // state
    isLoading,
    isError: error,

    /**
     * ‚úÖ Â§ñÈÉ®„Åã„Çâ‰Ωø„Åà„Çã mutate
     * submitFavorite / rollback / ÂÜçÊ§úË®º Áî®
     */
    mutateItemDetail: mutate,
  };
};
